.connect {
    display: inline-block;
    border-radius: 20px;
    margin: 1px;
    font-weight: bold;
    font-size: medium;
    padding: 5px;
    border-width: 1px;
    border-style: solid;
    background-color: #BBDEFB;
    /* background-color: #3CCCFC; */
}
.address {
    font-weight: bold;
    font-size: medium;
    border-style: none;
    background-color: #BBDEFB;
}
.button {
    border-style: none;
    padding: 2px;
    background-color: #BBDEFB;
}
.invalid {
    background-color: #FFCDD2;
}
